html{
    background-color: rgb(32, 32, 32);
    color: white;
}

.container{
    align-self:auto;
    width: 270px;
    height: 60px;

    margin: 50px auto;

    border: 1px solid gray;
    border-radius: 1px;
    padding: 6px;

    color: #000;
    background-color: rgba(249, 249, 249);
}

video{
    position: fixed;
    margin-left: -19%;
    margin-right: -19%;
    width: 138%;
}

.recaptcha{
    display: flex;
    align-items: center;
}

.checkbox{
    display:inline-block;
    margin-left: 12px;
    height: 20px;
    width: 20px;
    background-color: white;
    border: 2px solid rgb(193, 193, 193);
    border-radius: 2px;
    
    cursor: pointer
}

label{
    margin-left: 12px;
}

img{
    margin-left: 60px;
}

